.About-site {
   padding: 10px;
}

.about-summary {
   font-size: 25px;
   margin: 25px;
   text-align: center;
}

.about-description {
   display: flex;
   justify-content: space-between;
   align-items: start;
   margin-top: 50px;
}

.about-experience span {
   color: gray;
   text-decoration: underline;
}
.about-experience li h4, .about-experience li h4 a {
color: gold;
}
.about-experience li h4 a:hover {
   text-decoration: underline;
   }
.about-experience {
      width: 35%;
   }
.personal-qualities{
   width: 40%;
}
.about-certificate {
   display: flex;
   flex-direction: column;
   margin: 35px;
}

.about-certificate h2 {
   letter-spacing: 4px;
   text-align: center;
   margin: 35px;
}

.about-img {
   display: flex;
   justify-content: space-around;
   align-items: center;
   flex-wrap: wrap;
}

.about-img img {
   /* display: block; */
   width: 350px;
   height: 220px;
   margin: 10px 0;
   box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
}

.about-stack ul li,
.about-experience ul li,
.personal-qualities ul li {
   color: white;
   font-size: 18px;
   margin-left: 20px;
   margin-bottom: 15px;
}

.about-stack ul,
.about-experience ul,
.personal-qualities ul {
   border-left: 1px solid gray;
   margin-top: 15px;
}

.about-link-cv {
   text-align: right;
}

.about-link-cv a {
   text-decoration: underline;
   color: lightgray;
   font-weight: 800;
   font-size: 16px;
   letter-spacing: 1.5px;
}